html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: var(--font);
  font-weight: 400;
  font-size: 14px;
  min-height: 100%;
}

main {
  padding-bottom: 72px;
}

.wrapper {
  display: table;
    margin: auto;
    width: 100%;
  /*position: relative;*/
  /*min-height: 100%;  */

}

.container {
  @mixin container-fluid;
}